div {
  box-sizing: border-box;
}

.join {
  display: flex;
  margin-top: 8px;

  .right {
    margin-left: 4px;
  }
}

.row {
  margin-top: -1px;
  display: flex;
  height: 32px;
  border: 1px solid transparent;

  .data & {
    border-color: #ddd;
  }

  .header & {
    font-weight: bold;
  }
}

.cell {
  height: 32px;
  padding: 4px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
}

.null {
  font-family: Menlo, monospace;
  font-size: 0.7rem;
  color: #999;
}
